const _import = file => () => import(`@/views/${file}.vue`)

export default {
  path: '/project',
  name: 'Project',
  component: undefined,
  meta: {
    title: '项目管理',
    icon: 'fa fa-tasks',
  },
  children: [
    {
      path: '/project/list',
      component: _import('project/list'),
      name: 'ProjectList',
      meta: { title: '项目列表', icon: 'el-icon-house' }
    }
  ]
}
