/**
 * 管理员自身所属模块(与业务无关)
 */

import {
  post,
  get,
  put,
  _delete
} from '@/utils/request'
import { saveToken } from '../utils/cookie'

export default class User {
  /**
   * @static
   * @param {*} account 用户账号
   * @param {*} secret 密码
   */
  static async getToken(account, secret) {
    const data = await post('v1/token', {
      account,
      secret,
      type: 101,
    })
    saveToken(data.token)
  }

  /**
   * 获取当前用户信息
   */
  static async getInfo() {
    const user = await get('v1/user')
    return user
  }

  // 获取用户列表
  static async getList(id, size, page) {
    const data = await get(`/cms/user/list?company_id=${id}&page=${page}&size=${size}`)
    return data
  }

  // 新增用户
  static async createOne(user) {
    const data = await post('/cms/user', user)
    return data
  }

  // 编辑用户
  static async editOne(uid, user) {
    const data = await put(`/cms/user/${uid}`, user)
    return data
  }

  // 删除用户
  static async deleteOne(uid) {
    const data = await _delete(`/cms/user/${uid}`)
    return data
  }

  // 管理员获取用户信息
  static async getUserInfo(uid, user) {
    const data = await get(`/cms/user/${uid}`, user)
    return data
  }
}
