const _import = file => () => import(`@/views/${file}.vue`)

export default {
  path: '/category',
  name: 'Category',
  component: undefined,
  meta: {
    title: '设备类别管理',
    icon: 'fa fa-institution',
  },
  children: [
    {
      path: '/category/list',
      component: _import('device-category/list'),
      name: 'CategoryList',
      meta: { title: '设备类别', icon: 'el-icon-house' }
    },
  ]
}
