const _import = file => () => import(`@/views/${file}.vue`)

export default {
  path: '/device',
  name: 'Device',
  component: undefined,
  meta: {
    title: '设备管理',
    icon: 'fa fa-microchip',
  },
  children: [
    {
      path: '/device/list',
      component: _import('device/list'),
      name: 'DeviceList',
      meta: { title: '设备列表', icon: 'el-icon-house' }
    },
    {
      path: '/device/input',
      component: _import('device/input'),
      name: 'DeviceInput',
      meta: { title: '设备录入', icon: 'el-icon-house' }
    }
  ]
}
