const _import = file => () => import(`@/views/${file}.vue`)

export default {
  path: '/company',
  name: 'Company',
  component: undefined,
  meta: {
    title: '公司管理',
    icon: 'fa fa-institution',
  },
  children: [
    {
      path: '/company/list',
      component: _import('company/list'),
      name: 'CompanyList',
      meta: { title: '公司列表', icon: 'el-icon-house' }
    },
  ]
}
